import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../../router.animations";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { EmployeeService } from "src/app/shared/services/employee.service";
import { Router } from "@angular/router";

@Component({
    selector: "app-employee-add",
    templateUrl: "./add.component.html",
    styleUrls: ["./add.component.scss"],
    animations: [routerTransition()],
})
export class AddComponent implements OnInit {
    empAddForm: FormGroup;
    loading: boolean = false;
    submitted: boolean = false;
    constructor(
        private formBuilder: FormBuilder,
        private router: Router,
        private employeeService: EmployeeService
    ) {}

    ngOnInit() {
        this.empAddForm = this.formBuilder.group({
            firstname: ["", Validators.required],
            lastname: ["", Validators.required],
            number: [
                "",
                Validators.compose([
                    Validators.pattern("^[a-zA-Z0-9_]*$"),
                    Validators.required,
                ]),
            ],
            email: ["", [Validators.required, Validators.email]],
            phone: [
                "",
                Validators.compose([
                    Validators.minLength(10),
                    Validators.maxLength(10),
                    Validators.pattern("^[0-9]{1,10}"),
                    Validators.required,
                ]),
            ],
            joined_on: ["", Validators.required],
            acc_no: [
                "",
                Validators.compose([
                    Validators.pattern("^[0-9]*$"),
                    Validators.required,
                ]),
            ],
            ifsc_code: ["", Validators.required],
            salary: [
                "",
                Validators.compose([
                    Validators.pattern("^[0-9]*$"),
                    Validators.required,
                ]),
            ],
        });
    }

    get f() {
        return this.empAddForm.controls;
    }

    onSubmit() {
        this.submitted = true;
        // stop here if form is invalid
        console.log(this.empAddForm.value);
        if (this.empAddForm.invalid) {
            return;
        }

        this.loading = true;
        this.employeeService
            .add(this.empAddForm.value)
            .subscribe((response) => {
                this.router.navigate(["/employees/view"], {
                    queryParams: { id: response.emp_id },
                });
            });
    }
}
