import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../../router.animations";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { EmployeeService } from "src/app/shared/services/employee.service";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: "app-employee-edit",
    templateUrl: "./edit.component.html",
    styleUrls: ["./edit.component.scss"],
    animations: [routerTransition()],
})
export class EditComponent implements OnInit {
    empEditForm: FormGroup;
    loading: boolean = false;
    submitted: boolean = false;
    employeeId: any;

    constructor(
        private formBuilder: FormBuilder,
        private router: Router,
        private route: ActivatedRoute,
        private employeeService: EmployeeService
    ) {}

    ngOnInit() {
        this.route.queryParams.subscribe((params) => {
            // Defaults to 0 if no query param provided.
            this.employeeId = params["id"];
        });
        this.empEditForm = this.formBuilder.group({
            firstname: ["", Validators.required],
            lastname: ["", Validators.required],
            number: [
                "",
                Validators.compose([
                    Validators.pattern("^[a-zA-Z0-9_]*$"),
                    Validators.required,
                ]),
            ],
            email: ["", [Validators.required, Validators.email]],
            phone: [
                "",
                Validators.compose([
                    Validators.minLength(10),
                    Validators.maxLength(10),
                    Validators.pattern("^[0-9]{1,10}"),
                    Validators.required,
                ]),
            ],
            joined_on: ["", Validators.required],
            acc_no: [
                "",
                Validators.compose([
                    Validators.pattern("^[0-9]*$"),
                    Validators.required,
                ]),
            ],
            ifsc_code: ["", Validators.required],
            salary: [
                "",
                Validators.compose([
                    Validators.pattern("^[0-9]*$"),
                    Validators.required,
                ]),
            ],
        });
        this.employeeService.findById(this.employeeId).subscribe((response) => {
            const joined_on = response.employee.joined_on.split("-");
            const year = joined_on[0];
            const month = joined_on[1];
            const day = joined_on[2];
            this.empEditForm = this.formBuilder.group({
                firstname: [response.employee.firstname, Validators.required],
                lastname: [response.employee.lastname, Validators.required],
                number: [response.employee.number, Validators.required],
                email: [response.employee.email, Validators.required],
                phone: [
                    response.employee.phone,
                    Validators.compose([
                        Validators.minLength(10),
                        Validators.maxLength(10),
                        Validators.pattern("^[0-9]{1,10}"),
                        Validators.required,
                    ]),
                ],
                joined_on: [
                    {
                        year: parseInt(year, 10),
                        month: parseInt(month, 10),
                        day: parseInt(day, 10),
                    },
                    Validators.required,
                ],
                acc_no: [
                    response.employee.acc_no,
                    Validators.compose([
                        Validators.pattern("^[0-9]*$"),
                        Validators.required,
                    ]),
                ],
                ifsc_code: [response.employee.ifsc_code, Validators.required],
                salary: [
                    response.employee.salary,
                    Validators.compose([
                        Validators.pattern("^[0-9]*$"),
                        Validators.required,
                    ]),
                ],
            });
        });
    }

    get f() {
        return this.empEditForm.controls;
    }

    onSubmit() {
        this.submitted = true;
        // stop here if form is invalid
        if (this.empEditForm.invalid) {
            return;
        }

        this.loading = true;
        this.employeeService
            .update(this.empEditForm.value, this.employeeId)
            .subscribe((response) => {
                this.router.navigate(["/employees/view"], {
                    queryParams: { id: response.emp_id },
                });
            });
    }
}
